import 'base';
import 'jquery';
import * as sweetalert from 'sweetalert';

swal = sweetalert.default;

/**
 * @description 
 * @class GM.Tooltip
 * @ignore
 * @depercated
 */
GM.Tooltip = {
    /**
     * @description 
     */
    options: {
        placement: 'right',
        trigger: 'focus'
    },
    /**
     * @description 
     * @method show
     * @param {Object} $Ele jquery
     * @param {String} strMessage 
     * @param {Object} options 
     */
    show: function ($Ele, strMessage, options) {
        if ($Ele && $Ele.tooltip) {
            this.options = GM.Util.setOptions(this, options);
            this.options.title = strMessage;
            $Ele.tooltip(this.options).tooltip('show');
        }
    },
    /**
     * @description 
     * @method close
     * @param {Object} $Ele jquery
     */
    close: function ($Ele) {
        if ($Ele && $Ele.tooltip) {
            $Ele.tooltip('destroy');
        }
        $('.tooltip.fade.right').remove();
    }
};

/**
 * @description 
 * @class GM.Popover
 * @ignore
 * @depercated
 */
GM.Popover = {
    /**
     * @description 
     */
    options: {
        placement: 'bottom',
        trigger: 'focus',
        html: true
    },
    show: function ($Ele, strTitle, strContent, options) {
        if ($Ele && $Ele.tooltip) {
            this.options = GM.Util.setOptions(this, options);
            this.options.title = strTitle;
            this.options.content = strContent;
            $Ele.popover(this.options).popover('show');
        }
    },
    toggle: function ($Ele, strTitle, strContent, options) {
        if ($Ele && $Ele.tooltip) {
            this.options = GM.Util.setOptions(this, options);
            this.options.title = strTitle;
            this.options.content = strContent;
            this.options.trigger = 'manual';
            $Ele.popover(this.options).popover('toggle');
        }
    },
    close: function ($Ele) {
        if ($Ele && $Ele.tooltip) {
            $Ele.popover('destroy');
        }
    }
};

/**
 * @description Alert
 */
GM.Alert = {
    /**
     * 
     * @property {Object} options
     * @property {String} options.title="" 
     * @property {String} options.text="" 
     * @property {"warning"/"error"/"success"/"info"} options.type="info" 
     * @property {Boolean} options.allowOutsideClick=false 
     * @property {Boolean} options.showCancelButton=false 
     * @property {String} options.confirmButtonText="" 
     * @property {String} options.confirmButtonColor="#AEDEF4" 
     * @property {String} options.cancelButtonText="" 
     * @property {String} options.imageUrl=null 
     * @property {String} options.imageSize="80x80" 
     */
    options: {
        title: '',
        text: '',
        type: 'info',
        allowOutsideClick: false,
        showCancelButton: false,
        confirmButtonText: '',
        confirmButtonColor: '#AEDEF4',
        cancelButtonText: '',
        imageUrl: null,
        imageSize: '80x80'
    },
    /**
     * 
     *
     *```
     *GM.Alert.show("","",{
     *      type:"warning"
     *});
     *```
     *
     * @static
     * @method show
     * @param {String} strTitle 
     * @param {String} strContent 
     * @param {Object} options 
     * @param {Function} callback 
     *
     */
    show: function (strTitle, strContent, options, callback) {
        if (swal) {
            options = options || {};
            this.options = GM.Util.setOptions(this, options);
            this.options.title = strTitle;
            this.options.text = strContent;
            this.options.icon = options.type || 'info';
            this.options.closeOnClickOutside = options.allowOutsideClick || false;
            this.options.buttons = [{
                text: options.cancelButtonText || '',
                value: null,
                visible: this.options.showCancelButton || false,
                className: '',
                closeModal: true
            }, {
                text: options.confirmButtonText || '',
                value: true,
                visible: true,
                className: '',
                closeModal: true
            }];
            try {
                swal(this.options).then((value) => {
                    if (callback && value === true) {
                        callback();
                    }
                });
            } catch (ex) {
                alert(strContent);
            }
        }
    }
};

/**
 * @description 
 * @class GM.Infomation
 * @author LP
 * @static
 */
GM.Infomation = {
    /**
     * 
     * @property {Object} options
     * @property {'primary'|'success'|'info'|'warning'|'danger'|'important'|'special'} options.type="default" 
     * @property {'bottom'|'top-left'|'top-right'|'bottom-left'|'bottom-right'|'center'} options.placement="" 
     * @property {Number} options.time="4000 
     * @property {String} options.parent="body" 
     * @property {String} options.icon="info-sign" zui
     *                                              {@link GM.Infomation#getIcoFromType getIcoFromType}
     * @property {Boolean} options.close=true 
     * @property {Boolean} options.fade=true 
     * @property {Boolean} options.scale=true 
     */
    options: {
        /**
         * @description 'primary'|'success'|'info'|'warning'|'danger'|'important'|'special'
         */
        type: 'default',
        /**
         * @description  'bottom'|'top-left'|'top-right'|'bottom-left'|'bottom-right'|'center'
         */
        placement: 'top',
        /**
         * @description ms
         */
        time: 4000,
        /**
         * @description 
         */
        parent: 'body',
        /**
         * @description 
         */
        icon: 'info-sign',
        /**
         * @description 
         */
        close: true,
        /**
         * @description true
         */
        fade: true,
        /**
         * @description true
         */
        scale: true
    },
    /**
     * {@link GM.Alert#show GM.Alert}
     * @static
     * @method show
     * @param {String} strMessage 
     * @param options {GM.Infomation.options}
     *
     */
    show: function (strMessage, options) {
        this.options = GM.Util.setOptions(this, options);
        this.getIcoFromType();
        this.msg = new $.zui.Messager(strMessage, this.options);
        this.msg.show();
    },
    close: function () {
        this.msg.hide();
    },
    /**
     * @description 
     * @method getIcoFromType
     */
    getIcoFromType: function () {
        switch (this.options.type) {
        case 'default':
        case 'primary':
            this.options.icon = 'bell-alt';
            break;
        case 'success':
            this.options.icon = 'ok-sign';
            break;
        case 'info':
            this.options.icon = 'info-sign';
            break;
        case 'warning':
            this.options.icon = 'warning-sign';
            break;
        case 'danger':
            this.options.icon = 'exclamation-sign';
            break;
        case 'important':
            this.options.icon = 'star';
            break;
        case 'special':
            this.options.icon = 'heart';
            break;
        }
    }
};

/**
 * @description 
 * @depercated
 * @ignore
 */
GM.FullModal = {
    /**
     * @description 
     * @method show
     * @param {String} strTitle 
     * @param {String} strUrl 
     */
    show: function (strTitle, strUrl) {
        top.$.zui.modalTrigger.show({
            iframe: strUrl,
            size: 'fullscreen',
            title: strTitle,
            position: 'center',
            backdrop: 'static',
            moveable: true
        });
    },
    /**
     * @description 
     * @method showCustomModal
     * @param {String} strTitle 
     * @param {String} strUrl 
     * @param {Object} options 
     */
    showCustomModal: function (strTitle, strUrl, options) {
        options['iframe'] = strUrl;
        options['title'] = strTitle;
        top.$.zui.modalTrigger.show(options);
    }
};



