import 'angular';
import 'angular-ui-tree';

/**
 * 
 * @class NgModule.tree
 * @alias gm.ngCustom.tree
 * @author LP
 */
angular.module('gm.ngCustom.tree', ['gm.ngCustom.utils']).provider('$gmTree', function () {
    this.$get = [function () {
        /**
         * @class NgModule.tree.$gmTree
         * @method constructor
         * @cfg {Array} treeNode 
         * @cfg {String} attChildren 
         * @alias $gmTree
         * @author LP
         */
        function TableFactory (treeNode, attChildren) {
            var $gmTree = {};

            /**
             * @member NgModule.tree.$gmTree
             * @method isLastLeafNode 
             * @param {Array} treeNode 
             * @param {Object} node 
             * @return {Boolean} 
             */
            $gmTree.isLastLeafNode = function (treeNode, node) {
                if (treeNode == null) {
                    return false;
                }
                for (var ii = 0; ii < treeNode.length; ii++) {
                    var pItemNode = treeNode[ii];
                    if (pItemNode[attChildren] == null || pItemNode[attChildren].length === 0) {
                        if (pItemNode === node && ii === treeNode.length - 1) {
                            return true;
                        }
                    } else {
                        var bRt = $gmTree.isLastLeafNode(pItemNode[attChildren], node);
                        if (bRt === true) {
                            return true;
                        }
                    }
                }
                return false;
            };

            /**
             * @member NgModule.tree.$gmTree
             * @method getParentNode 
             * @param {Object} node 
             * @return {Object|null} 
             */
            $gmTree.getParentNode = function (node) {
                var result = GM.TreeOper.getParentNode(treeNode, node, attChildren);
                return result;
            };

            /**
             * @member NgModule.tree.$gmTree
             * @method checkChildren 
             * @param {Object} pNode 
             * @param {Boolean} bChecked 
             */
            $gmTree.checkChildren = function (pNode, bChecked) {
                if (pNode == null) {
                    return;
                }
                if (pNode[attChildren]) {
                    for (var ii = 0; ii < pNode[attChildren].length; ii++) {
                        var pItemNode = pNode[attChildren][ii];
                        pItemNode.check = bChecked;
                        $gmTree.checkChildren(pItemNode, bChecked);
                    }
                }
            };

            /**
             * @member NgModule.tree.$gmTree
             * @method getCheckedNodes 
             * @return {Array} 
             */
            $gmTree.getCheckedNodes = function () {
                var result = $gmTree.getNodeFromParams(treeNode, 'check', true);
                return result;
            };

            /**
             * @member NgModule.tree.$gmTree
             * @method getNodeFromParams 
             * @param {Array} arrayNodes 
             * @param {String} strName 
             * @param {String} strValue 
             * @return {Array} 
             */
            $gmTree.getNodeFromParams = function (arrayNodes, strName, strValue) {
                if (arrayNodes == null) {
                    arrayNodes = treeNode;
                }
                var result = GM.TreeOper.getNodesByParam(arrayNodes, strName, strValue, attChildren);
                return result;
            };

            /**
             * @member NgModule.tree.$gmTree
             * @method formatNodes 
             * @param {Array} arrayNodes 
             * @param {String} attName 
             * @param {String} bCollapsed 
             * @return {Array} 
             */
            $gmTree.formatNodes = function (arrayNodes, attName, bCollapsed) {
                if (arrayNodes == null) {
                    return false;
                }
                for (var ii = 0; ii < arrayNodes.length; ii++) {
                    var pNode = arrayNodes[ii];
                    if (pNode.collapsed == null) {
                        pNode.collapsed = bCollapsed;
                    }
                    if (pNode.html == null && pNode.nodeType != null) {
                        pNode.html = GM.CommonOper.formatString("<span class='tree-span-text node-{0}'>{1}</span>", [pNode.nodeType, pNode[attName]]);
                    }
                    if (pNode[attChildren]) {
                        $gmTree.formatNodes(pNode[attChildren], attName, bCollapsed);
                    } else {
                        //
                        pNode.collapsed = pNode.parent || false;
                    }
                }
            };

            return $gmTree;
        }
        return TableFactory;
    }];
}).directive('gmTree', ['$gmTree',
    /**
     *
     * ```
     * 
     * $scope.config = {
     *      checkNode:"checkNode",
     *      activeNode:"activeNode",
     *      //name:"title",
     *      //children:""
     *  };
     *   $scope.checkNode = function(arrayNodes){
     *      console.log(arrayNodes);
     *  };
     *   $scope.activeNode = function(node){
     *      console.log(node);
     *  };
     *
     *
     *<gm-tree treedata="treeData" data-check = true data-icon = true config = "config"  showquery=true></gm-tree>
     *
     * ```
     *
     * @member NgModule.tree
     * @method gmTree 
     * @param {Array} treedata 
     * @param {Boolean} [check=false] 
     * @param {Boolean} [icon=false] 
     * @param {Boolean} [showquery=false] 
     * @param {Object} config 
     * @param {String} [config.name = "name"] 
     * @param {String} [config.children = "children"] 
     * @param {String} [config.checkNode] 
     * @param {String} [config.activeNode] 
     * @param {String} [collapsed = "true"] DOM
     * @param {String} [checkRel = "true"] DOM
     */
    function ($gmTree) {
        return {
            restrict: 'ECA',
            template: require('./tree.tpl.html'),
            scope: {
                treedata: '=',
                check: '=',
                icon: '=',
                config: '=',
                showquery: '=',
                treetoggle: '='
            },
            link: function postLink (scope, element, attr) {
                if (scope.config == null) {
                    scope.config = {};
                }
                scope.attName = scope.config.name ? scope.config.name : 'name';
                scope.attChildren = scope.config.children ? scope.config.children : 'children';
                scope.nodeCollapsed = attr['collapsed'] !== 'false'; //
                scope.checkRel = attr['checkRel'] !== 'false';
                scope.bClickNode = false;
                var pGmTree = null;

                //
                scope.treeToggle = function (scope, node) {
                    if (scope.collapsed && angular.isFunction(scope.treetoggle)) {
                        scope.treetoggle(node);
                    }
                    scope.toggle();
                };
                scope.visible = function (node) {
                    return true;
                };
                //
                scope.checkNode = function (node) {
                    if (node == null) {
                        return;
                    }
                    node.check = !node.check;

                    if (scope.checkRel === true) {
                        //
                        //1.
                        pGmTree.checkChildren(node, node.check);
                        //2.
                        GM.TreeOper.updateNodeChecked(scope.treedata, scope.attChildren, null);
                    }

                    if (scope.config && angular.isFunction(scope.$parent[scope.config.checkNode])) {
                        var arrayCheckedNodes = pGmTree.getCheckedNodes();
                        scope.$parent[scope.config.checkNode](arrayCheckedNodes, node);
                    }
                };

                //
                scope.getNodeClass = function (node) {
                    //var bRt = pGmTree.isLastLeafNode(scope.treedata, node);
                    return '';
                    //if (bRt === true) {
                    //    return 'icon-leafnode-last';
                    //} else {
                    //    return 'icon-leafnode';
                    //}
                };

                //
                scope.getNodeIconClass = function (node, collapsed) {
                    if ((node[scope.attChildren] == null || node[scope.attChildren].length === 0) && node.parent !== true) {
                        return 'icont-file';
                    } else {
                        if (node.iconClass === 'icon-globe') {
                            return 'icon-globe';
                        } else if (node.iconClass === 'icon-building') {
                            return 'icon-building';
                        } else if (node.iconClass === 'icon-home') {
                            return 'icon-home';
                        } else if (collapsed === true) {
                            return 'icont-folder-close';
                        } else if (collapsed === false) {
                            return 'icont-folder-open';
                        }
                    }
                };

                //
                scope.clickNode = function (pNode) {
                    scope.activeNode = pNode;
                    if (scope.config && angular.isFunction(scope.$parent[scope.config.activeNode])) {
                        scope.$parent[scope.config.activeNode](pNode);
                    }
                    scope.bClickNode = true;
                };

                //
                scope.clickDiv = function ($event) {
                    if (scope.bClickNode === false) {
                        $event.stopPropagation();
                    }
                    scope.bClickNode = false;
                };

                //
                scope.$watch('treedata', function (newValue, oldValue, scope) {
                    pGmTree = new $gmTree(scope.treedata, scope.attChildren);
                    pGmTree.formatNodes(scope.treedata, scope.attName, scope.nodeCollapsed);

                    //
                    if (scope.checkRel === true) {
                        GM.TreeOper.updateNodeChecked(scope.treedata, scope.attChildren, null);
                    }
                });
            }
        };
    }
]).directive('gmComboTree', ['$gmTree',
    /**
     *
     * ```
     * 
     * <gm-combo-tree treedata="treeData" binddata="test" config = "config" showquery=true></gm-combo-tree>
     * ```
     *
     * @member NgModule.tree
     * @method gmComboTree 
     * @param {Array} treedata 
     * @param {String} binddata 
     * @param {Boolean} [showquery=false] 
     * @param {String} gm-validator DOM
     * @param {Object} config 
     * @param {String} [config.name = "name"] 
     * @param {String} [config.children = "children"] 
     * @param {String} [config.selectNode] 
     */
    function ($gmTree) {
        return {
            restrict: 'ECA',
            template: require('./combo-tree.tpl.html'),
            scope: {
                treedata: '=',
                binddata: '=',
                config: '=',
                showquery: '=',
                gmDisable: '='
            },
            compile: function compile (element, attr, transclude) {
                return {
                    pre: function preLink (scope, element, attr, controller) {
                        var binddata = attr['binddata'];
                        scope.binddataName = binddata;
                    },
                    post: function postLink (scope, element, attr, controller) {
                        if (scope.config == null) {
                            scope.config = {};
                        }
                        scope.attName = scope.config.name ? scope.config.name : 'name';
                        scope.attChildren = scope.config.children ? scope.config.children : 'children';
                        scope.strAttValue = scope.config.attvalue ? scope.config.attvalue : 'value';
                        scope.config.activeNode = 'activeNode';
                        scope.validator = attr['gmValidator'] == null ? 'none' : attr['gmValidator'];

                        var binddata = attr['binddata'];
                        var pGmTree = new $gmTree(scope.treedata, scope.attChildren);

                        //
                        scope.activeNode = function (pNode) {
                            var strValue = pNode[scope.strAttValue];
                            if (strValue != null) {
                                GM.CommonOper.setObjectValue(scope.$parent, binddata, strValue);
                            }
                            scope.viewText = pNode[scope.attName];
                            if (GM.CommonOper.isFunction(scope.$parent[scope.config.selectNode])) {
                                scope.$parent[scope.config.selectNode](pNode);
                            }
                        };

                        //
                        scope.$watch('binddata', function (newValue, oldValue, scope) {
                            var arrayNode = pGmTree.getNodeFromParams(null, scope.strAttValue, newValue);
                            if (arrayNode != null && arrayNode.length > 0) {
                                scope.viewText = arrayNode[0][scope.attName];
                            } else {
                                if (newValue === undefined || newValue === '') {
                                    scope.viewText = newValue;
                                }
                            }
                        });
                    }
                };
            }
        };
    }
]).filter('queryTree', function () {
    //
    return function (treeNodes, queryVal, attName, attChildren) {
        var result = [];
        if (GM.CommonOper.isStrNullOrEmpty(queryVal)) {
            return treeNodes;
        }
        if (treeNodes) {
            for (var ii = 0; ii < treeNodes.length; ii++) {
                var tempResult = GM.TreeOper.getNodesLikeParam([treeNodes[ii]], attName, queryVal, attChildren);
                if (tempResult.length > 0) {
                    result.push(treeNodes[ii]);
                }
            }
        }
        return result;
    };
});



