import 'angular';
import 'angular-validation';

/**
 * 
 *
 * ```
 * 
 * <input name="ywh" validator="required,minlength=10">
 * ```
 *
 * @class NgModule.validation.rule
 * @alias validation.rule
 * @author LP
 */
angular
    .module('validation.rule', ['validation'])
    .config(['$validationProvider', function ($validationProvider) {
        var expression = {
            /**
             * @member NgModule.validation.rule
             * @method required 
             */
            required: function (value) {
                return !!value;
            },
            /**
             * @member NgModule.validation.rule
             * @method url 
             */
            url: /((([A-Za-z]{3,9}:(?:\/\/)?)(?:[-;:&=+\$,\w]+@)?[A-Za-z0-9.-]+|(?:www.|[-;:&=+\$,\w]+@)[A-Za-z0-9.-]+)((?:\/[+~%/.\w-_]*)?\??(?:[-+=&;%@.\w_]*)#?(?:[\w]*))?)/,
            /**
             * @member NgModule.validation.rule
             * @method email 
             */
            email: /^([\w-\.]+)@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.)|(([\w-]+\.)+))([a-zA-Z]{2,4}|[0-9]{1,3})(\]?)$/,
            /**
             * @member NgModule.validation.rule
             * @method number 
             * @param {Number} param 
             */
            number: function (value, scope, element, attrs, param) {
                if (GM.CommonOper.isStrNullOrEmpty(value)) {
                    return true;
                }
                var re = /^\d*(\.\d+)?$/g;
                var bRt = re.test(value);
                return bRt;
            },
            /**
             * @member NgModule.validation.rule
             * @method minlength 
             * @param {Number} param 
             */
            minlength: function (value, scope, element, attrs, param) {
                return value && value.length >= param;
            },
            /**
             * @member NgModule.validation.rule
             * @method maxlength 
             * @param {Number} param 
             */
            maxlength: function (value, scope, element, attrs, param) {
                if (GM.CommonOper.isStrNullOrEmpty(value)) {
                    return true;
                }
                return !value || value.length <= param;
            },
            /**
             * @member NgModule.validation.rule
             * @method maxcharlength 
             * @param {Number} param 
             */
            maxcharlength: function (value, scope, element, attrs, param) {
                if (GM.CommonOper.isStrNullOrEmpty(value)) {
                    return true;
                }
                if (Number.isNaN(Number(param))) {
                    return false;
                }
                var nLength = GM.CommonOper.getCharLength(value);
                return nLength <= Number(param);
            },
            /**
             * @member NgModule.validation.rule
             * @method mincharlength 
             * @param {Number} param 
             */
            mincharlength: function (value, scope, element, attrs, param) {
                if (Number.isNaN(Number(param))) {
                    return false;
                }
                var nLength = GM.CommonOper.getCharLength(value);
                return nLength >= Number(param);
            },
            /**
             * @member NgModule.validation.rule
             * @method numberleter 
             * @param {String} param 
             */
            numberleter: function (value, scope, element, attrs, param) {
                if (GM.CommonOper.isStrNullOrEmpty(value)) {
                    return true;
                }
                var re = /^(\d|[a-z]|[A-Z])+$/g;
                var bRt = re.test(value);
                return bRt;
            },
            /**
             * @member NgModule.validation.rule
             * @method maxnum 
             * @param {Number} param 
             */
            maxnum: function (value, scope, element, attrs, param) {
                if (GM.CommonOper.isStrNullOrEmpty(value) || Number.isNaN(Number(param))) {
                    return true;
                }
                var bRt = Number(value) <= Number(param);
                return bRt;
            },
            /**
             * @member NgModule.validation.rule
             * @method minnum 
             * @param {Number} param 
             */
            minnum: function (value, scope, element, attrs, param) {
                if (GM.CommonOper.isStrNullOrEmpty(value) || Number.isNaN(Number(param))) {
                    return true;
                }
                var bRt = Number(value) >= Number(param);
                return bRt;
            },
            /**
             * @member NgModule.validation.rule
             * @method dlength 
             * @param {Number} param 
             */
            dlength: function (value, scope, element, attrs, param) {
                if (value == null) {
                    return true;
                }
                var bRt = String(value).length === Number(param);
                return bRt;
            },
            /**
             * @member NgModule.validation.rule
             * @method normalchar 
             * @param {String} param 
             */
            normalchar: function (value, scope, element, attrs, param) {
                if (GM.CommonOper.isStrNullOrEmpty(value)) {
                    return true;
                }
                var re = /([\u4e00-\u9fa5]|\w)+$/g;
                var bRt = re.test(value);
                return bRt;
            },
            /**
             * @member NgModule.validation.rule
             * @method chinese 
             * @param {String} param 
             */
            chinese: function (value, scope, element, attrs, param) {
                if (GM.CommonOper.isStrNullOrEmpty(value)) {
                    return true;
                }
                var re = /[\u4e00-\u9fa5]+$/g;
                var bRt = re.test(value);
                return bRt;
            },
            /**
             * @member NgModule.validation.rule
             * @method phone 
             * @param {String} param 
             */
            phone: function (value, scope, element, attrs, param) {
                if (GM.CommonOper.isStrNullOrEmpty(value)) {
                    return true;
                }
                var re = /^\d+-?\d+$/g;
                var bRt = re.test(value);
                return bRt;
            },
            /**
             * @member NgModule.validation.rule
             * @method ip ip
             * @param {String} ip
             */
            ip: function (value, scope, element, attrs, param) {
                if (GM.CommonOper.isStrNullOrEmpty(value)) {
                    return true;
                }
                var re = /^(\d+)\.(\d+)\.(\d+)\.(\d+)$/g;
                var bRt = re.test(value);
                return bRt;
            },
            /**
             * @member NgModule.validation.rule
             * @method idcard 
             * @param {String} param
             */
            idcard: function (value, scope, element, attrs, param) {
                if (GM.CommonOper.isStrNullOrEmpty(value)) {
                    return true;
                }
                var idcard, Y, JYM, ereg;
                idcard = value;
                if (idcard == null || idcard === '') {
                    return false;
                }
                var Errors = [true, false, false, false, false];
                var S, M;
                var idcardArray = [];
                idcardArray = idcard.split('');
                //
                //if(area[parseInt(idcard.substr(0,2))]==null) return Errors[4];
                //
                switch (idcard.length) {
                case 15:
                    if ((parseInt(idcard.substr(6, 2)) + 1900) % 4 === 0 || ((parseInt(idcard.substr(6, 2)) + 1900) % 100 === 0 && (parseInt(idcard.substr(6, 2)) + 1900) % 4 === 0)) {
                        ereg = /^[1-9][0-9]{5}[0-9]{2}((01|03|05|07|08|10|12)(0[1-9]|[1-2][0-9]|3[0-1])|(04|06|09|11)(0[1-9]|[1-2][0-9]|30)|02(0[1-9]|[1-2][0-9]))[0-9]{3}$/; //
                    } else {
                        ereg = /^[1-9][0-9]{5}[0-9]{2}((01|03|05|07|08|10|12)(0[1-9]|[1-2][0-9]|3[0-1])|(04|06|09|11)(0[1-9]|[1-2][0-9]|30)|02(0[1-9]|1[0-9]|2[0-8]))[0-9]{3}$/; //
                    }
                    if (ereg.test(idcard)) return Errors[0];
                    else return Errors[2];
                case 18:
                    //18 
                    //
                    //:((01|03|05|07|08|10|12)(0[1-9]|[1-2][0-9]|3[0-1])|(04|06|09|11)(0[1-9]|[1-2][0-9]|30)|02(0[1-9]|[1-2][0-9]))
                    //:((01|03|05|07|08|10|12)(0[1-9]|[1-2][0-9]|3[0-1])|(04|06|09|11)(0[1-9]|[1-2][0-9]|30)|02(0[1-9]|1[0-9]|2[0-8]))
                    if (parseInt(idcard.substr(6, 4)) % 4 === 0 || (parseInt(idcard.substr(6, 4)) % 100 === 0 && parseInt(idcard.substr(6, 4)) % 4 === 0)) {
                        ereg = /^[1-9][0-9]{7}[0-9]{2}((01|03|05|07|08|10|12)(0[1-9]|[1-2][0-9]|3[0-1])|(04|06|09|11)(0[1-9]|[1-2][0-9]|30)|02(0[1-9]|[1-2][0-9]))[0-9]{3}[0-9Xx]$/; //
                    } else {
                        ereg = /^[1-9][0-9]{7}[0-9]{2}((01|03|05|07|08|10|12)(0[1-9]|[1-2][0-9]|3[0-1])|(04|06|09|11)(0[1-9]|[1-2][0-9]|30)|02(0[1-9]|1[0-9]|2[0-8]))[0-9]{3}[0-9Xx]$/; //
                    }
                    if (ereg.test(idcard)) { //
                        //
                        S = (parseInt(idcardArray[0]) + parseInt(idcardArray[10])) * 7 +
                                (parseInt(idcardArray[1]) + parseInt(idcardArray[11])) * 9 +
                                (parseInt(idcardArray[2]) + parseInt(idcardArray[12])) * 10 +
                                (parseInt(idcardArray[3]) + parseInt(idcardArray[13])) * 5 +
                                (parseInt(idcardArray[4]) + parseInt(idcardArray[14])) * 8 +
                                (parseInt(idcardArray[5]) + parseInt(idcardArray[15])) * 4 +
                                (parseInt(idcardArray[6]) + parseInt(idcardArray[16])) * 2 +
                                parseInt(idcardArray[7]) * 1 +
                                parseInt(idcardArray[8]) * 6 +
                                parseInt(idcardArray[9]) * 3;
                        Y = S % 11;
                        M = 'F';
                        JYM = '10X98765432';
                        M = JYM.substr(Y, 1); //
                        if (M === idcardArray[17]) return Errors[0]; //ID
                        else return Errors[3];
                    } else return Errors[2];
                case 10:
                    //
                    ereg = /^[A-Z]\d{6}[(|]\d[)|]$/g;
                    if (ereg.test(idcard)) {
                        return Errors[0];
                    } else {
                        return false;
                    }
                default:
                    return Errors[1];
                }
            },
            none: function () {
                return true;
            },
            /**
             * @member NgModule.validation.rule
             * @method Capital 
             */
            Capital: /^[A-Z]+$/,
            /**
             * @member NgModule.validation.rule
             * @method capital 
             */
            capital: /^[A-Z]*_?[A-Z]+$/
        };

        var defaultMsg = {
            required: {
                error: '',
                success: 'It\'s Required'
            },
            url: {
                error: '',
                success: 'It\'s Url'
            },
            email: {
                error: '',
                success: 'It\'s Email'
            },
            number: {
                error: '',
                success: 'It\'s Number'
            },
            minlength: {
                error: '',
                success: 'Long enough!'
            },
            maxlength: {
                error: '',
                success: 'Short enough!'
            },
            maxcharlength: {
                error: '',
                success: 'Short enough!'
            },
            mincharlength: {
                error: '',
                success: 'Short enough!'
            },
            maxnum: {
                error: ''
            },
            minnum: {
                error: ''
            },
            dlength: {
                error: ''
            },
            normalchar: {
                error: ''
            },
            numberleter: {
                error: ''
            },
            chinese: {
                error: ''
            },
            phone: {
                error: ''
            },
            ip: {
                error: 'ip'
            },
            idcard: {
                error: ''
            },
            none: {
                error: ''
            },
            Capital: {
                error: '',
                success: 'It\'s Capital'
            },
            capital: {
                error: '',
                success: 'It\'s capital'
            }
        };
        $validationProvider.setExpression(expression).setDefaultMsg(defaultMsg);
    }]);



